@media screen and (min-width: 1200px) {
    .app {
      max-width: 1280px;
      margin: 0 auto;
    }
}

.mainDashboard {
    display: flex;
    justify-content: center;
}

.sectionHeader{
    text-align: left;
}

.sectionColumn{
    width: 50%;
    flex-basis: content;
    margin-right: 72px;
    height: 85vh;
    overflow: auto;
}

ul::-webkit-scrollbar {
    width: 12px;
}

ul::-webkit-scrollbar-track {
    background: #2F2F37;
}

ul::-webkit-scrollbar-thumb {
    background: #8585AD;
	border: 3px solid #2F2F37;
	border-radius: 6px;
}

div::-webkit-scrollbar {
    width: 12px;
}

div::-webkit-scrollbar-track {
    background: #2F2F37;
}

div::-webkit-scrollbar-thumb {
    background: #8585AD;
	border: 3px solid #2F2F37;
	border-radius: 6px;
}


